.blogs{
  .main-heading::before{
    height: 17px;
  }
  .searsh{
    width: 100%;
    input{
      display: inline-block;
      width: 78%;
      border: 1px solid #CCC;
      padding: 10px;
    }
    button{
      text-align: center;
      color: #fff;
      background-color: #222;
      display: inline-block;
      min-width: 13%;
      padding: 10px
    }
  }
  .categorie{
    li{
      font-size: 18px;
      line-height: 1.8;
      color: #6a6a6a;
      transition: all .2s;
      cursor: pointer;
      &:hover{
        color: $mainColor;
        padding-left: 5px;
      }
    }
  }
  .tags{
    li{
      color: #6a6a6a;
      display: inline-block;
      padding: 10px;
      border: 1px solid #ccc;
      margin-left: 10px;
      margin-bottom: 10px;
      cursor: pointer;
      transition: all .2s;
      &:hover{
        background-color: $mainColor;
        color: #fff;
        border-color: transparent
      }
    }
  }
  .last-blogs{
    img{
      width: 70px
    }
    a{
      color: #000;
      font-size: 16px;
      margin: 0;
      transition: all .2s;
      display: block;
      &:hover{
        color: $mainColor
      }
    }
    span{
      color: #6a6a6a;
      font-size: 13px;

    }
  }
}
