.navs{
  border: 1px solid #CCC;
  padding: 25px 10px 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  nav{
    border-bottom: 0;
    li{
      a{
        color: $mainGrey;
        font-weight: bold;
        &:hover, &.active{
          background-color: $mainColor;
          color: #FFF
        }
      }
    }
  }
}
