*{
  box-sizing: border-box
}
body{
  font-family: 'Open Sans', sans-serif;
  overflow-x: hidden;
}
a:hover{
  text-decoration: none
}
input, textarea, button{
  border: 0;
  outline: none;
  &:focus{
    outline: none;
  }
}
img{
  object-fit: contain
}
.main-heading{
  position: relative;
  color: #000;
  font-family: 'Neucha', cursive;
  letter-spacing: 2px;
  margin-left: 15px;
  &::before{
    content: '';
    position: absolute;
    top: 0;
    left: -10px;
    width: 4px;
    height: 30px;
    background-color: $mainColor
  }
}
.add-to-cart{
  padding: 10px;
  background-color: $mainColor;
  color: #FFF;
  font-weight: bold;
  display: block;
  margin: 5px 0;
  &:hover{
    opacity: .9
  }
}
.triangle{
  position: relative;
  &::after{
    position: absolute;
    content: '';
    display: block;
    left: calc(50% - 40px);
    bottom: -78px;
    border: 40px solid;
    border-color: #f0f0f0 transparent transparent transparent
  }
}
