.cart-title{
  display: inline-block;
  text-align: center
}
.table-container{
  overflow-x: auto;
  table{
    color: #222;
    font-family: Arial;
    thead{
      color: $mainColor;
    }
    td{
      vertical-align: middle;
      min-width: 100px
    }
    .name{
      font-size: 18px;
      min-width: 260px;
    }
    .p-image{
      width: 170px
    }
    .price, .total{
      font-weight: bold;
      font-size: 20px;
    }
    .counter{
      font-size: 22px;
      color: #000
    }
    button{
      background-color: $mainColor;
      color: #FFF;
      padding: 5px 10px;
      transition: all .3s;
      border: 2px solid transparent;
      &:hover{
        background-color: #FFF;
        color: $mainColor;
        border-color: $mainColor
      }
    }
    .remove-item{
      color: #000;
      font-size: 22px;
      transition: all .3s;
      cursor: pointer;
      &:hover{
        color: $mainColor;
      }
    }
  }
}
.cart-details{
  background-color: #f0f0f0;
  padding: 10px;
  border: 1px solid #CCC;
  padding: 20px;
  .total-price{
    color: #000;
    font-weight: bold
  }
  .col-6{
    align-items: center;
  }
  a{
    color: #FFF;
    font-size: 16px;
    background-color: $mainGrey;
    padding: 7px 0;
    display: block;
    width: 150px;
    margin: 5px auto;
    border: 2px solid transparent;
    border-radius: 20px;
    transition: all .2s;
    &:hover{
      background-color: #fff;
      color: $mainColor;
      border-color: $mainColor
    }
  }
  .b{
    border-top: 1px solid #CCC
  }
}
