.sub-nav{
  .breadcrumb{
    margin: 0;
    padding-top: 35px;
    padding-bottom: 35px;
  }
}
.account{
  min-height: 80vh;
  background: url("../images/login.jpg") no-repeat;
  background-size: cover;
  .forms-container{
    width: 50%;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0px 0px 10px #444;
    background-color: #f0f0f0;
    min-height: 400px;
    .titles{
      display: flex;
      align-items: baseline;
      justify-content: space-between;
      position: relative;
      border-bottom: 1px solid #CCC;
      h3{
        margin: 10px 10px -1px;
        color: $mainGrey;
        padding: 15px 20px;
        cursor: pointer;
        transition: all .5s;
        border: 1px solid transparent;
        &::selection{
          background-color: transparent;
          color: inherit
        }
        &.active, &:hover{
          border: 1px solid #ccc;
          border-bottom-color: #fff;
          background-color: #FFF;
        }
      }
    }
    form{
      background-color: #fff;
    }
    #sign-in{
      display: none
    }
    input{
      display: block;
      padding: 10px;
      border: 1px solid #CCC;
      margin-top: 10px;
      width: 95%;
      margin-left: auto;
      margin-right: auto;
      &:focus{
        border-left: 4px solid $mainColor;
      }
    }
    input[type="submit"]{
      font-size: 20px;
      color: #FFF;
      width: 150px;
      margin:20px auto;
      background-color: $mainGrey;
      border: 2px solid transparent;
      transition: all .2s;
      padding: 7px 0;
      display: block;
      &:hover{
        background-color: #fff;
        color: $mainColor;
        border-color: $mainColor
      }
      &:focus{
        border-left: 0;
      }
    }
    a{
      display: block;
      margin: 7px auto;
      font-size: 14px;
      color: #222;
      &:hover{
        text-decoration: underline;
      }
    }
  }
}
@media(max-width: 768px){
  .account{
    .forms-container{
      width: 60%
    }
  }
}
@media(max-width: 567px){
  .account{
    background-position: 60% 100%;
    .forms-container{
      width: 90%
    }
  }
}
