.blog-details{
  .b-image{
    width: 100%;
    img{
      width: 100%;
    }
    h3{
      color: #000
    }
    .blog-info{
      display: flex;
      justify-content: space-between;
      color: $mainGrey;
      font-size: 14px
    }
  }
  p{
    font-size: 16px;
    color: #000;
    background-color: #f6f6f6;
    padding: 15px 10px;
    border-left: 2px solid $mainColor;
    line-height: 1.6
  }
  .share-to{
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    display: flex;
    padding: 15px 10px;
    justify-content: space-between;
    align-items: center;
    a{
      padding: 5px;
      background-color: #f4f4f4;
      color: #000;
      &:hover{
        text-decoration: none;
      }
    }
  }
  .fa-facebook{color: #3b5998}
  .fa-twitter{color: #1da1f2}
  .fa-google-plus-g{color: #dd4b39}
  .fa-pinterest-p{color: #bd081c}
  .fa-linkedin{color: #0077b5}
  form{
    input, textarea{
      display: block;
      margin-top: 10px;
      padding: 7px;
      background-color: #f0f0f0;
      border: 1px solid #CCC;
      width: 100%;
      border-left: 2px solid $mainColor
    }
    textarea{
      border-left: 1px solid #CCC;
      resize: vertical;
      height: 150px
    }
    input[type="submit"]{
      color: #FFF;
      font-size: 16px;
      background-color: $mainGrey;
      padding: 10px 0;
      display: block;
      width: 150px;
      margin:20px 0;
      border: 2px solid transparent;
      transition: all .2s;
      &:hover{
        background-color: #fff;
        color: $mainColor;
        border-color: $mainColor
      }
    }
  }
}
