header{
  min-height: 80vh;
  background: url('../images/57172.jpg') no-repeat 85% 50%;
  background-size: cover;
  padding-top: 10%;
  padding-bottom: 10%;
  transition: all .25s;
  position: relative;
  overflow-x: hidden;
  &::before{
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: -100%;
    background: linear-gradient(125deg, transparent, rgba(255, 255, 255, .2), transparent);
    transition: all .4s;
  }
  &:hover::before{
    left: 100%
  }
  h1{
    font-size: 3.5rem;
    color: #FFF;
    width: 50%;
    font-weight: bold;
    text-shadow: 0px 0px 10px #000;
  }
  button{
    display: block;
    padding: 10px 15px;
    font-weight: bold;
    background-color: #FFF;
    font-size: 18px;
    border-radius: 30px;
    transition: all .3s ease-out;
    position: relative;
    overflow: hidden;
    a{
      color: $mainRed;
    }
    &:hover{
      background-color: $mainColor;
      a{
        color: #fff;
      }
    }
  }
}
@media(max-width: 768px){
  header{
    min-height: 60vh;
    padding-top: 20%;
    h1{
      font-size: 2rem;
      width: 80%;
    }
    button{
      padding: 10px;
      font-size: 18px
    }
  }
}
@media(max-width: 567px){
  header{
    text-align: center;
    h1{
      width: 100%
    }
    button{
      margin-left: auto;
      margin-right: auto
    }
  }
}

/*main product*/
.main-product{
  h4{
    color: #222;
    font-weight: bold
  }
  .review i{
    color: $mainYellow;
    font-size: 18px;
  }
  .price{
    color: $mainColor;
    font-weight: bold;
    span{
      font-size: 16px;
      color: #828282;
      text-decoration: line-through;
    }
  }
  p{
    margin-top: -1px
  }
  button{
    float: right;
  }
}

/*features section*/
.features{
  background-color: #f0f0f0;
  padding: 20px 5px 40px;
  .box{
    span{
      position: relative;
      border: 3px solid $mainColor;
      display: flex;
      padding: 7px;
      justify-content: center;
      align-items: center;
      width: 50px;
      height: 50px;
      margin: 10px auto;
      &::before{
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        top: 7px;
        left: -7px;
        background-color: #FFF;
      }
      i{
        font-size: 22px;
        z-index: 2;
        transform: translate(-7px, 7px)
      }
    }
  }
}

/*Features products*/
.products-container{
  margin: 10px 15px;
  .box{
    .product-img{
      background-color: #f0f0f0;
      height: 250px;
      cursor: pointer;
      img{
        transform: scale(.8);
        width: 100%;
        height: 100%;
        transition: all .3;
      }
      .sold{
        position: absolute;
        top: 3px;
        left: 3px;
        background-color: $mainColor;
        color: #FFF;
        padding: 3px 7px;
        border-radius: 3px
      }
      .icons{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: rgba(0, 0, 0, .5);
        text-align: center;
        overflow:hidden;
        opacity: 0;
        transition: all .3s;
        span{
          display: inline-block;
          background-color: #FFF;
          padding: 10px;
          margin-top: 5px;
          margin-left: 5px;
          cursor: pointer;
          transform: translateY(-65px);
          transition: all .3s ease-in-out;
          i{
            font-size: 20px;
            color: $mainGrey;
            transition: all .2s;
            &:hover{
              color: $mainColor
            }
          }
        }
        span:first-of-type{
          transition-delay: .1s
        }
      }
      &:hover{
        .icons{
          opacity: 1;
          span{
            transform: translateY(0px);
          }
        }
        img{
          transform: rotateY(180deg) scale(.8);
        }
      }
    }
    .description{
      h5{
        color: $mainColor;
        font-weight: bold;
        color: #000;
        span{
          font-size: 16px;
          color: #828282;
          text-decoration: line-through;
        }
      }
      .price{
        color: $mainColor;
        font-weight: bold;
        span{
          font-size: 16px;
          color: #828282;
          text-decoration: line-through;
        }
      }
    }
  }
}
@media (max-width: 567px){
  .products-container{
    .box{
      .product-img{
        .icons{
          background-color: rgba(0, 0, 0, .1);
          opacity: 1;
          span{
            transform: translateY(0);
          }
        }
      }
    }
  }
}
/*Abou us section*/
.about{
  p{
    line-height: 2
  }
  a{
    width: 200px;
    display: inline-block;
    padding: 10px;
    text-align: center;
    background-color: $mainColor;
    color: #FFF;
    font-weight: bold;
    margin-top: 10px;
    margin-bottom: 20px;
    &:hover{
      opacity: .9
    }
  }
  .image{
    position: relative;
    span{
      position: absolute;
      width: 100%;
      height: 100%;
      border: 4px solid $mainColor;
      z-index: 0;
      top: 15px;
      left: 15px
    }
  }
}

/*shopping section*/
.shopping{
  min-height: 250px;
  width: 100%;
  margin-top: 100px;
  background: url('../images/shop1.jpg') no-repeat 90% 100% fixed;
  background-size: cover;
  padding: 15px 5px;
  color: #FFF;
  display: flex;
  justify-content: center;
  align-items: center;
  a{
    padding: 10px;
    background-color: $mainColor;
    color: #FFF;
    font-weight: bold;
    display:  inline-block;
    margin: 5px 0;
    transition: all .3s;
    &:hover{
      background-color: $mainRed
    }
  }
}
/*blogs*/
.blogs{
  h5{
    color: #000;
    font-weight: bold
  }
  p{
    font-size: 14px;
    margin-left: 5px
  }
  a{
    color: $mainRed;
    &:hover{
      text-decoration: underline;
    }
  }
  .link{
    display: flex;
    align-items: center;
    justify-content: space-between;
    span{
      font-size: 14px
    }
  }
}
/*Our Brands*/
.brands{
  padding: 20px;
  border-top: 1px solid #f0f0f0;
  border-bottom: 1px solid #f0f0f0;
  i{
    color: #828282
  }
  .owl-nav{
    display: none !important;
  }
}
/*Veiw products modal*/
.view-product{
  color: #555;
  .modal-header{
    background-color: #f0f0f0;
  }
  #myLargeModalLabel{
    font-weight: bold;
    color: #000;
  }
  .p-img{
    border-right: 2px solid #CCC;
    height: 100%;
    img{
      transition: all .2s;
      transform: scale(.8);
    }
    &:hover img{
      transform: rotateY(180deg) scale(.8);
    }
  }
  .p-name{
    color: #000
  }
  .p-price{
    color: $mainColor;
    font-weight: bold;
    span{
      font-size: 16px;
      color: #828282;
      text-decoration: line-through;
    }
  }
  .p-review{
    i{
      color: $mainYellow
    }
  }
  .p-description{
    .card-header{
      background-color: #fff;
      border-bottom: 0;
      h5{
        button{
          color: #555
        }
      }
    }
  }
  .share{
    a{
      display: inline-block;
      background-color: #CCC;
      margin-right: 7px;
      padding:4px 7px;
      color: $mainGrey;
      font-size: 12px;
      margin-top: 7px;
      .fa-facebook{color: #3b5998}
      .fa-twitter{color: #1da1f2}
      .fa-google-plus-g{color: #dd4b39}
      .fa-pinterest-p{color: #bd081c}
    }
  }
  .modal-footer{
    a{
      color: #FFF
    }
  }
}
