nav{
  border-bottom: 1px solid #ccc;
  .nav-child{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    .small-cart{
      position: absolute;
      right: 10px;
      width: 300px;
      top: 75px;
      background-color: #fff;
      z-index: 100;
      border-top: 2px solid $mainColor;
      padding: 15px 0;
      display: none;
      p{
        text-align: center;
      }
      li{
        padding: 10px;
        border-bottom: 1px solid #ccc;
        display: flex;
        justify-content: space-around;
        color: #333;
        span{
          color: #000;
          font-weight: bold;
        }
        img{
          width: 45px;
          height: 45px
        }
        &:hover{
          background-color: #f0f0f0;
        }
      }
    }
    .logo{
      max-width: 150px;
      img{
        width: 100%;
        height: auto;
        margin-left: -22px;
      }
    }
    .nav-links ul{
      margin: 0;
      li{
        display: inline-block;
        margin-left: 10px;
        a{
          color: $mainGrey;
          font-weight: 550;
          transition: all .3s;
          border-top: 2px solid transparent;
          &.active, &:hover{
            color: $mainColor;
            border-top-color: $mainColor;
            padding-top: 5px
          }
        }
      }
    }
    .cart-icons{
      i{
        font-size: 24px;
        margin-right: 15px;
        color: #626262;
        cursor: pointer;
        transition: all .3s;
        &:hover{
          color: $mainColor
        }
      }
      .cart{
        #has-item{
          position: absolute;
          width: 20px;
          height: 20px;
          background-color: $mainRed;
          top: -15px;
          left: 15px;
          border-radius: 50%;
          color: #fff;
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }
      .humbergur{
        display: inline-block;
        cursor: pointer;
        span{
          display: block;
          width: 30px;
          height: 3px;
          margin-top: 5px;
          background-color: #686868;
          transition: all .3s ease-in-out;
        }
        span:last-of-type, span:first-of-type{
          width: 15px;
        }
        &:hover{
          span:last-of-type, span:first-of-type{
            width: 30px;
          }
        }
        &:hover span{
          background-color: $mainColor;
        }
      }
    }
    /*&:hover .small-cart{
      opacity: 0
    }*/
  }
}
.searsh-input{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 40px;
  background-color: #e9ecef;
  z-index: 3;
  display: none;
  input[type="text"]{
    display: inline-block;
    width: 80%;
    text-indent: 20px;
    padding: 10px;
    background-color: transparent;
  }
  input[type="submit"]{
    display: inline-block;
    width: 18%;
    padding: 10px;
    background-color: $mainGrey;
    color: #fff;
    font-weight: bold;
    transition: all .5s;
    &:hover{
      background-color: #000
    }
  }
}
.fixed-nav{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  background-color: rgba(0, 0, 0, .6);
  z-index: 100;
  overflow-x: hidden;
  opacity: 0;
  pointer-events: none;
  .the-navbar{
    position: absolute;
    right: -50%;
    top: 0;
    width: 50%;
    min-height: 100%;
    background-color: #FFF;
    text-align: center;
    padding-top: 5%;
    transition: all .3s ease-in-out;
    .searsh{
      width: 100%;
      input[type="text"]{
        border: 1px solid #ccc;
        display: inline-block;
        width: 80%;
        text-indent: 20px;
        padding: 8px;
      }
      button{
        display: inline-block;
        width: 18%;
        padding: 10px;
        background-color: $mainGrey;
        color: #fff;
        font-weight: bold;
        transition: all .5s;
        &:hover{
          background-color: #000
        }
      }
    }
    ul li{
      line-height: 3;
      a{
        color: $mainGrey;
        transition: all .3s;
        font-size: 20px;
        &.active, &:hover{
          color: $mainColor;
        }
      }
    }
    hr{
      width: 80%
    }
    .icons i{
      transition: all .3s;
      margin-left: 10px;
      font-size: 18px;
      cursor: pointer;
      &:hover{
        color: $mainColor
      }
    }
    &.show{
      right: 0;
    }
  }
  .close{
    position: absolute;
    left: 7px;
    top: -7px;
    cursor: pointer;
    font-size: 40px;
  }
  &.open{
    opacity: 1;
    pointer-events: all;
  }
}

@media(max-width: 768px){
  nav .nav-links{
    display: none
  }
}
@media(max-width: 576px){
  .fixed-nav{
    .the-navbar{
      width: 90%;
      right: -90%;
      padding-left: 5px;
      padding-right: 5px;
      padding-bottom: 10px;
    }
  }
}
