footer{
  background-color: $mainGrey;
  padding: 15px 0 0;
  .subscribe{
    padding: 10px 5px 20px;
    border-bottom: 1px solid #535353;
    p{
      color: #b9b9b9
    }
    form{
      width: 95%;
      display: block;
      margin-left: auto;
      margin-right: auto;
      input[type="text"]{
        width: 100%;
        background-color: transparent;
        border: 1px solid #CCC;
        padding: 9px;
        color: #fff;
      }
      input[type="submit"]{
        width: 100%;
        padding: 10px;
        background-color: $mainColor;
        color: #FFF;
        font-weight: bold;
        margin-top: 10px;
        &:hover{
          opacity: .9
        }
      }
    }
  }
  .footer{
    .logo-container{
      max-width: 150px;
      img{
        margin-left: -22px;
      }
    }
    .p{
      color: #b9b9b9;
      font-size: 15px;
      b{
        color: #FFF
      }
    }
    .help-links{
      ul li{
        color: #b9b9b9;
        line-height: 2;
        cursor: pointer;
        font-size: 15px;
        transition: all .3s;
        &:hover{
          margin-left: 7px;
          color: $mainColor;
        }
      }
    }
    .other{
      color: #b9b9b9;
      span{
        display: block;
        font-size: 15px;
        i{
          color: $mainColor;
          margin-right: 10px
        }
      }
      .social-media{
        display: flex;
        align-items: center;
        justify-content: space-between;
        i{
          font-size: 22px;
          transition: all .3s;
          margin-right: 10px;
          cursor: pointer;
          &:hover{
            color: #FFF;
          }
        }
      }
      .payment{
        padding: 10px;
        border-bottom: 1px solid #535353;
        border-top: 1px solid #535353;
        i{
          font-size: 20px;
          margin-right: 15px
        }
      }
    }
  }
  .copy{
    border-top: 1px solid #535353;
    color: #b9b9b9;
    margin: 0;
    font-size: 13px;
    padding: 5px
  }
}
@media(min-width: 768px){
  footer{
    .subscribe{
      form{
        width: 70%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        input[type="text"]{
          width: 82%;
        }
        input[type="submit"]{
          width: 16%;
          margin-top: 0
        }
      }
    }
  }
}
