.contact{
  .contact-us{
    p{
      font-size: 13px;
      margin-left: 7px;
      color: #6f6f6f
    }
    form{
      input, textarea{
        display: block;
        margin-top: 10px;
        padding: 7px;
        background-color: #f0f0f0;
        border: 1px solid #CCC;
        width: 100%;
        border-left: 2px solid $mainColor
      }
      textarea{
        border-left: 1px solid #CCC;
        resize: vertical;
        height: 150px
      }
      input[type="submit"]{
        color: #FFF;
        font-size: 16px;
        background-color: $mainGrey;
        padding: 10px 0;
        display: block;
        width: 150px;
        margin:20px 0;
        border: 2px solid transparent;
        transition: all .2s;
        &:hover{
          background-color: #fff;
          color: $mainColor;
          border-color: $mainColor
        }
      }
    }
  }
  .find-us{
    p{
      color: #222
    }
    ul{
      border-top: 1px solid #CCC;
      li{
        border-bottom: 1px solid #ccc;
        padding: 10px;
        color: #333;
        i{
          color: $mainColor;
          margin-right: 10px 
        }
      }
    }
  }
}
